import { Component, Prop, State, Event, EventEmitter, Watch, Listen, h, Host } from '@stencil/core';
import { SelectedDate, DisabledDate } from '../bh-date-picker-content/bh-date-picker-content';
import { SelectedDateRange } from '../bh-date-range-picker-content/bh-date-range-picker-content';

export type DateRangePickerVariant = 'dropdown-outlined' | 'dropdown-ghost' | 'input';

@Component({
  tag: 'bh-date-range-picker',
  styleUrl: 'bh-date-range-picker.css',
  shadow: true,
})
export class BhDateRangePicker {
  /**
   * Variant of the date range picker trigger
   */
  @Prop() variant: DateRangePickerVariant = 'input';

  /**
   * Minimum year in the picker
   */
  @Prop() minYear: number = 1998;

  /**
   * Maximum year in the picker
   */
  @Prop() maxYear: number = 2060;

  /**
   * Whether the picker is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Array of disabled dates
   */
  @Prop() disabledDates: DisabledDate[] = [];

  /**
   * Label for start date input
   */
  @Prop() startLabel: string = 'Start Date';

  /**
   * Label for end date input
   */
  @Prop() endLabel: string = 'End Date';

  /**
   * Placeholder for start date
   */
  @Prop() startPlaceholder: string = 'Choose start date';

  /**
   * Placeholder for end date
   */
  @Prop() endPlaceholder: string = 'Choose end date';

  /**
   * Width for input variant
   */
  @Prop() inputWidth: string = '200px';

  /**
   * Whether to show the label
   */
  @Prop() showLabel: boolean = true;

  /**
   * Whether to show the footer
   */
  @Prop() showFooter: boolean = true;

  /**
   * Whether to show days from other months
   */
  @Prop() showOtherMonthDays: boolean = false;

  /**
   * Currently selected value
   */
  @Prop() value?: SelectedDateRange;

  @State() selectedValue: SelectedDateRange | null = null;
  @State() tempSelection: SelectedDateRange | null = null;
  @State() isMenuOpen: boolean = false;
  @State() isMenuClosing: boolean = false;

  /**
   * Event emitted when value changes
   */
  @Event() bhChange!: EventEmitter<SelectedDateRange | null>;

  /**
   * Event emitted when a date range is selected
   */
  @Event() bhDateRangeSelect!: EventEmitter<SelectedDateRange | null>;

  @Watch('value')
  handleValueChange(newValue: SelectedDateRange | undefined) {
    if (newValue !== undefined) {
      this.selectedValue = newValue;
    }
  }

  componentWillLoad() {
    if (this.value) {
      this.selectedValue = this.value;
    }
  }

  private formatDate(date: SelectedDate | null): string {
    if (!date) return '';
    const month = (date.month + 1).toString().padStart(2, '0');
    const day = date.day.toString().padStart(2, '0');
    return `${month}/${day}/${date.year}`;
  }

  private getButtonLabel(): string {
    if (!this.selectedValue || !this.selectedValue.startDate) return 'Select date range';

    const startStr = this.formatDate(this.selectedValue.startDate);
    const endStr = this.selectedValue.endDate ? this.formatDate(this.selectedValue.endDate) : '';

    if (endStr) {
      return `${startStr} - ${endStr}`;
    }
    return startStr;
  }

  private handleTriggerClick = () => {
    if (this.disabled) return;
    if (!this.isMenuOpen && this.showFooter) {
      this.tempSelection = this.selectedValue;
    }
    this.isMenuOpen = !this.isMenuOpen;
  };

  @Listen('bhDateRangeSelect')
  handleDateRangeSelectEvent(event: CustomEvent<SelectedDateRange | null>) {
    // Stop propagation to prevent the event from bubbling further
    event.stopPropagation();

    const range = event.detail;
    if (this.showFooter) {
      // With footer: update temp selection, user clicks Apply to confirm
      this.tempSelection = range;
    } else {
      // Without footer: update value immediately, close only when range is complete
      this.selectedValue = range;
      if (range?.startDate && range?.endDate) {
        this.bhChange.emit(range);
        this.bhDateRangeSelect.emit(range);
        this.closeMenu();
      }
    }
  }

  private handleMenuCancel = () => {
    this.tempSelection = null;
    this.closeMenu();
  };

  private handleMenuApply = () => {
    if (this.tempSelection) {
      this.selectedValue = this.tempSelection;
      this.bhChange.emit(this.tempSelection);
      this.bhDateRangeSelect.emit(this.tempSelection);
      this.tempSelection = null;
    }
    this.closeMenu();
  };

  private closeMenu() {
    this.isMenuClosing = true;
    setTimeout(() => {
      this.isMenuOpen = false;
      this.isMenuClosing = false;
    }, 150);
  }

  private handleBackdropClick = () => {
    this.handleMenuCancel();
  };

  render() {
    return (
      <Host>
        <div class="date-range-picker-container">
          {/* Dropdown Outlined Variant */}
          {this.variant === 'dropdown-outlined' && (
            <div class="date-range-picker-trigger" onClick={this.handleTriggerClick}>
              <bh-button
                hierarchy="secondary"
                
                size="sm"
                icon="trailing"
                label={this.getButtonLabel()}
                iconName="expand_more"
                disabled={this.disabled}
              ></bh-button>
            </div>
          )}

          {/* Dropdown Ghost Variant */}
          {this.variant === 'dropdown-ghost' && (
            <div class="date-range-picker-trigger" onClick={this.handleTriggerClick}>
              <bh-button
                hierarchy="tertiary"
                
                size="sm"
                icon="trailing"
                label={this.getButtonLabel()}
                iconName="expand_more"
                disabled={this.disabled}
              ></bh-button>
            </div>
          )}

          {/* Input Variant */}
          {this.variant === 'input' && (
            <div class="date-range-picker-trigger">
              <div class="date-range-inputs" onClick={this.handleTriggerClick}>
                <bh-input-text
                  label={this.startLabel}
                  placeholder={this.startPlaceholder}
                  value={this.formatDate(this.selectedValue?.startDate || null)}
                  disabled={this.disabled}
                  showTrailingIcon={true}
                  trailingIcon="calendar_today"
                  showHelpIcon={false}
                  showHintText={false}
                  showLabel={this.showLabel}
                  width={this.inputWidth}
                  readOnly={true}
                ></bh-input-text>
                <bh-input-text
                  label={this.endLabel}
                  placeholder={this.endPlaceholder}
                  value={this.formatDate(this.selectedValue?.endDate || null)}
                  disabled={this.disabled}
                  showTrailingIcon={true}
                  trailingIcon="calendar_today"
                  showHelpIcon={false}
                  showHintText={false}
                  showLabel={this.showLabel}
                  width={this.inputWidth}
                  readOnly={true}
                ></bh-input-text>
              </div>
            </div>
          )}

          {/* Date Range Picker Menu */}
          <div class="date-range-picker-menu-container">
            <bh-picker-menu
              visible={this.isMenuOpen}
              showFooter={this.showFooter}
              disableApply={this.showFooter && (!this.tempSelection?.startDate || !this.tempSelection?.endDate)}
              onBhCancel={this.handleMenuCancel}
              onBhApply={this.handleMenuApply}
            >
              <bh-date-range-picker-content
                minYear={this.minYear}
                maxYear={this.maxYear}
                disabled={this.disabled}
                disabledDates={this.disabledDates}
                showOtherMonthDays={this.showOtherMonthDays}
                value={this.tempSelection ?? this.selectedValue ?? undefined}
              ></bh-date-range-picker-content>
            </bh-picker-menu>
          </div>
        </div>

        {/* Backdrop */}
        {(this.isMenuOpen || this.isMenuClosing) && (
          <div class="date-range-picker-backdrop" onClick={this.handleBackdropClick}></div>
        )}
      </Host>
    );
  }
}
